import Unity, { UnityContext } from "react-unity-webgl";
import React, { useState,useEffect } from "react";
export const SkyClimbers = () => {
  const [progression, setProgression] = useState(0);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(function () {
    unityContext.on("progress", function (progression) {
      setProgression(progression);
    });
    unityContext.on("loaded", function () {
      setIsLoaded(true);
    });

    return () => {
      unityContext.on("quitted", function () {
        // var canvas = $("canvas");
        // console.log("quit game",canvas);
        // canvas.remove();
      });
    };
  }, []);

  const unityContext = new UnityContext({
    loaderUrl: "Build/Sky Climbers WEBGL.loader.js",
    dataUrl: "Build/Sky Climbers WEBGL.data",
    frameworkUrl: "/Build/Sky Climbers WEBGL.framework.js",
    codeUrl: "Build/Sky Climbers WEBGL.wasm",
  });
  return (
    <div style={{height: '100vh'}}>
      <p  style={{ visibility: isLoaded ? "hidden" : "visible" }}>Loading {progression * 100} percent...</p>
      <Unity  style={{ visibility: isLoaded ? "visible" : "hidden" }} unityContext={unityContext} style={{ width: "90%", height: "auto" }}/>
    </div>
  );
}